@media (min-width: 811px) {
  .ProductReviews {
    z-index: 1; } }

.ProductReviews-Title {
  margin: 0 0 .6rem;
  font-size: 2.4rem; }
  @media (max-width: 810px) {
    .ProductReviews-Title {
      margin: 0 0 .7rem;
      font-size: 2.8rem; } }
  @media (max-width: 810px) {
    .ProductReviews-Title {
      display: none; } }

.ProductReviews-Wrapper {
  padding: 0; }
  @media (min-width: 811px) {
    .ProductReviews-Wrapper {
      padding: 3.6rem 1.2rem; } }

.ProductReviews-SummaryRating {
  font-size: 3rem; }

@media (min-width: 811px) {
  .ProductReviews-ExpandableContentHeading, .ProductReviews-ExpandableContentButton {
    display: none; } }

@media (min-width: 811px) {
  .ProductReviews-ExpandableContentContent {
    margin-top: 0; } }

.ProductReviews-ExpandableContentContent_isContentExpanded {
  padding-top: 0; }

.ProductReviews .ProductReviews-Button {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: max-content;
  margin: auto; }
  @media (max-width: 810px) {
    .ProductReviews .ProductReviews-Button {
      position: relative;
      height: auto;
      width: 100%;
      margin-top: 1.2rem; } }
  @media (max-width: 810px) {
    .ProductReviews .ProductReviews-Button {
      margin-top: 1.4rem; } }

.ProductReviews-SummaryDetails {
  display: inline-block;
  margin-left: 1.2rem;
  font-size: 2rem; }
  @media (max-width: 810px) {
    .ProductReviews-SummaryDetails {
      margin-left: 1.4rem; } }
  .ProductReviews-SummaryDetails span {
    font-size: 1.6rem;
    font-weight: normal; }
    .ProductReviews-SummaryDetails span::before {
      content: ' / '; }
