@media (min-width: 811px) {
  .ProductReviewForm-Wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 2.4rem; } }

.ProductReviewForm-ProductName {
  font-weight: 700;
  font-size: 2.4rem; }
  @media (max-width: 810px) {
    .ProductReviewForm-ProductName {
      font-size: 2.52rem; } }

@media (max-width: 810px) {
  .ProductReviewForm-Content {
    margin-top: 2.8rem; } }

.ProductReviewForm-Rating {
  margin: 1.2rem 0 1.56rem;
  text-align: left;
  direction: rtl;
  border: 0;
  /* stylelint-disable-next-line unknownProperties, property-no-unknown */
  white-space-collapse: discard; }
  @media (max-width: 810px) {
    .ProductReviewForm-Rating {
      margin: 1.4rem 0 1.82rem; } }
  @media (max-width: 810px) {
    .ProductReviewForm-Rating:first-child {
      margin-top: 0; } }

.ProductReviewForm-Legend {
  border-bottom: 0;
  padding-bottom: .36rem;
  font-weight: 700;
  font-size: 1.26rem; }
  @media (max-width: 810px) {
    .ProductReviewForm-Legend {
      padding-bottom: .42rem;
      font-size: 1.47rem; } }

.ProductReviewForm .ProductReviewForm-Button {
  margin-top: 1.2rem; }
  @media (max-width: 810px) {
    .ProductReviewForm .ProductReviewForm-Button {
      margin-top: 2.8rem;
      width: 100%; } }

.ProductReviewForm input,
.ProductReviewForm textarea {
  width: 100%; }
