.ProductReviewList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.4rem;
  margin-top: 2.4rem; }
  @media (max-width: 810px) {
    .ProductReviewList {
      margin-top: 2.8rem;
      grid-template-columns: 1fr;
      grid-gap: 1.4rem; } }
  .ProductReviewList-Title {
    text-align: center;
    font-weight: 700;
    font-size: 2.4rem;
    margin-top: 3.6rem; }
    @media (max-width: 810px) {
      .ProductReviewList-Title {
        font-size: 2.8rem;
        margin-top: 4.2rem; } }
