:root {
  --wishlist-heart-size: 12px;
  --wishlist-heart-background: var(--primary-base-color); }

.ProductWishlistButton {
  --loader-scale: .6;
  margin-left: 1.08rem;
  vertical-align: middle;
  display: inline-block; }
  @media (max-width: 810px) {
    .ProductWishlistButton {
      margin-left: 1.26rem; } }
  @media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
    .ProductWishlistButton {
      margin-top: 1.2rem; } }
  .ProductWishlistButton-Button {
    --button-padding: 0;
    --button-hover-background: transparent;
    --button-hover-border: var(--primary-dark-color);
    height: 47px;
    width: 47px; }
    .ProductWishlistButton-Button_isInWishlist {
      --wishlist-heart-background: #fff;
      --button-hover-background: var(--primary-dark-color);
      --button-hover-border: var(--primary-dark-color); }
      @media (min-width: 811px) {
        .ProductWishlistButton-Button_isInWishlist:hover {
          --wishlist-heart-background: #fff; } }
    .ProductWishlistButton-Button.Button {
      border-radius: 50%; }
  @media (min-width: 811px) {
    .ProductWishlistButton:hover {
      --wishlist-heart-background: var(--primary-dark-color); } }
  .ProductWishlistButton-Heart {
    transform: rotate(-45deg);
    bottom: 0;
    top: 1px;
    margin: auto; }
    .ProductWishlistButton-Heart, .ProductWishlistButton-Heart::after, .ProductWishlistButton-Heart::before {
      height: var(--wishlist-heart-size);
      width: var(--wishlist-heart-size);
      background-color: var(--wishlist-heart-background); }
    .ProductWishlistButton-Heart::after, .ProductWishlistButton-Heart::before {
      content: '';
      border-radius: 50%;
      position: absolute; }
    .ProductWishlistButton-Heart::before {
      top: calc(var(--wishlist-heart-size) / -2);
      right: 0; }
    .ProductWishlistButton-Heart::after {
      right: calc(var(--wishlist-heart-size) / -2);
      top: 0; }
