:root {
  --video-play-background: #000;
  --video-play-icon-fill: #fff;
  --video-play-icon-transform: scale(1); }

.VideoThumbnail {
  display: flex;
  justify-content: center;
  align-items: center; }
  .VideoThumbnail-Button {
    width: 100%; }
    .VideoThumbnail-Button:active, .VideoThumbnail-Button:hover {
      --video-play-icon-transform: scale(1.2); }
  .VideoThumbnail-Thumbnail {
    padding-bottom: 75%; }
  .VideoThumbnail-PlayIcon {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 0;
    height: 0;
    border-top: 30px solid transparent;
    border-left: 60px solid var(--video-play-icon-fill);
    border-bottom: 30px solid transparent;
    transform: var(--video-play-icon-transform);
    transition: transform 150ms ease-in;
    overflow: hidden; }
