.CarouselScrollArrow {
  height: 40px;
  z-index: 1;
  transition: opacity 200ms; }
  .CarouselScrollArrow::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(135deg);
    transition: transform 200ms;
    display: block;
    height: 15px;
    width: 15px;
    border-style: solid;
    border-width: 0 0 2px 2px; }
  .CarouselScrollArrow:hover::before {
    transform: translate(-50%, -50%) scale(1.2) rotate(135deg); }
  .CarouselScrollArrow_isNextArrow::before {
    transform: translate(-50%, -50%) rotate(-45deg); }
  .CarouselScrollArrow_isNextArrow:hover::before {
    transform: translate(-50%, -50%) scale(1.2) rotate(-45deg); }
  .CarouselScrollArrow_isDisabled {
    opacity: 0;
    cursor: default; }
