.VideoPopup {
  --popup-min-width: none;
  --popup-content-padding: 0; }
  @media (max-width: 810px) {
    .VideoPopup {
      --popup-content-padding: 0; } }
  .VideoPopup-VideoPlayer {
    width: 100vw;
    height: calc(100vh - 40px);
    background: black; }
    @media (min-width: 811px) {
      .VideoPopup-VideoPlayer {
        width: calc(100vw - 130px);
        height: 0;
        padding-top: 56.3%;
        max-width: calc((100vh - 300px) / .563); } }
  .VideoPopup-PlayerContent {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
    .VideoPopup-PlayerContent .vimeo {
      height: 100%;
      width: 100%; }
      .VideoPopup-PlayerContent .vimeo iframe {
        width: 100%;
        height: 100%; }
  .VideoPopup-YouTube, .VideoPopup-YouTubeContainer {
    height: 100%;
    width: 100%; }
  .VideoPopup .Popup-Header {
    display: none; }
