@media (min-width: 811px) {
  .ProductGallery {
    display: flex;
    height: 100%; } }

.ProductGallery-Additional {
  width: 150px;
  margin-right: 1.8rem; }
  @media (max-width: 810px) {
    .ProductGallery-Additional {
      margin-right: 2.1rem; } }
  @media (min-width: 811px) {
    .ProductGallery-Additional {
      height: 48rem;
      margin: auto 1.8rem auto 0; } }
  @media (max-width: 810px) {
    .ProductGallery-Additional {
      display: none; } }
  .ProductGallery-Additional_isImageZoomPopupActive {
    display: flex;
    flex-direction: column;
    justify-content: center; }

.ProductGallery-SliderWrapper {
  flex: 1; }

.ProductGallery-Slider {
  min-height: 480px;
  height: var(--slider-height); }
  @media (max-width: 810px) {
    .ProductGallery-Slider {
      min-height: 420px; } }
  @media (min-width: 811px) {
    .ProductGallery-Slider::after {
      content: '';
      box-shadow: inset 0 0 0 3px #fff;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      pointer-events: none; } }
  .ProductGallery-Slider_isImageZoomPopupActive {
    cursor: pointer; }
  @media (min-width: 811px) {
    .ProductGallery-Slider_isZoomInCursor {
      cursor: zoom-in; } }

.ProductGallery .react-transform-component,
.ProductGallery .react-transform-element, .ProductGallery-SliderImage {
  height: 100%;
  width: 100%; }

.ProductGallery-SliderImage .Image-Image {
  transform: translateY(var(--translateYOnCursorMove)); }

.overscrollPrevented {
  overscroll-behavior: contain; }
