.CarouselScroll {
  --translateY: 0;
  --animation-speed: 300ms;
  --carousel-item-height: 0;
  height: 100%;
  display: flex;
  flex-direction: column; }
  .CarouselScroll-ContentWrapper {
    overflow: hidden;
    flex: 1; }
  .CarouselScroll-Content {
    height: 100%;
    transform: translateY(var(--translateY));
    transition: transform var(--animation-speed); }
