.GroupedProductsItem {
  display: grid;
  padding: 0;
  margin-bottom: 1.2rem;
  grid-template-columns: 7.2rem 3fr 1fr;
  grid-gap: 1.2rem; }
  @media (max-width: 810px) {
    .GroupedProductsItem {
      margin-bottom: 1.4rem;
      grid-template-columns: 8.4rem 3fr 1fr;
      grid-gap: 1.4rem; } }
  .GroupedProductsItem::before {
    content: none; }
  .GroupedProductsItem:last-child {
    margin-bottom: 0; }
  .GroupedProductsItem-Image::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: var(--product-card-picture-mask-color);
    mix-blend-mode: multiply;
    pointer-events: none; }
  .GroupedProductsItem-Title {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 1.2rem;
    padding-bottom: 1.2rem;
    overflow: hidden; }
    @media (max-width: 810px) {
      .GroupedProductsItem-Title {
        padding-top: 1.4rem;
        padding-bottom: 1.4rem; } }
    .GroupedProductsItem-Title p {
      margin-bottom: 1.2rem;
      margin-top: 0;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      max-width: 90%; }
      @media (max-width: 810px) {
        .GroupedProductsItem-Title p {
          margin-bottom: 1.4rem; } }
