:root {
  --customizable-options-additional-information-color: #2c2c2c;
  --customizable-options-required-text-color: #dc6d6d; }

.ProductCustomizableOptions-Content {
  max-width: 400px; }
  .ProductCustomizableOptions-Content .Field input,
  .ProductCustomizableOptions-Content .Field textarea {
    width: 100%; }
  .ProductCustomizableOptions-Content .Field_type_checkbox {
    display: flex; }
    .ProductCustomizableOptions-Content .Field_type_checkbox label {
      order: 1; }
      .ProductCustomizableOptions-Content .Field_type_checkbox label:first-of-type ~ label {
        margin-left: 0; }
    .ProductCustomizableOptions-Content .Field_type_checkbox .Field-Label {
      margin-left: 1.2rem;
      order: 2; }
      @media (max-width: 810px) {
        .ProductCustomizableOptions-Content .Field_type_checkbox .Field-Label {
          margin-left: 1.4rem; } }

.ProductCustomizableOptions-Required {
  color: var(--customizable-options-required-text-color);
  margin-top: .6rem; }
  @media (max-width: 810px) {
    .ProductCustomizableOptions-Required {
      margin-top: .7rem; } }

.ProductCustomizableOptions-Information {
  color: var(--customizable-options-additional-information-color);
  font-size: 1.1rem;
  margin-top: .6rem; }
  @media (max-width: 810px) {
    .ProductCustomizableOptions-Information {
      margin-top: .7rem; } }

@media (max-width: 810px) {
  .ProductCustomizableOptions-ExpandableContentContent_isContentExpanded {
    padding: 0 1.2rem 1.2rem; } }

@media (max-width: 810px) {
  .ProductCustomizableOptions-ExpandableContentContent_isContentExpanded {
    padding: 0 1.4rem 1.4rem; } }

.ProductCustomizableOptions-ExpandableContentContent_isContentExpanded .Field:first-child {
  margin-top: 0; }

@media (min-width: 811px) {
  .ProductCustomizableOptions-ExpandableContentHeading {
    font-weight: normal; } }

.ProductCustomizableOptions-ExpandableContentHeading .ExpandableContent-HeadingAdditional {
  font-weight: bold; }

.ProductCustomizableOptions-Heading {
  font-weight: normal; }
  .ProductCustomizableOptions-HeadingBold {
    font-weight: bold; }

.ProductCustomizableOptions_isPlaceholder {
  animation: var(--placeholder-animation);
  background-image: var(--placeholder-image);
  background-size: var(--placeholder-size);
  max-width: 270px; }
