.ReviewStar {
  --star-outline-color: var(--secondary-dark-color);
  display: inline;
  margin: 0;
  padding: 0;
  background: none;
  border: 0;
  transition: color 150ms ease-in;
  color: #fff;
  text-shadow: -1px 0 var(--star-outline-color), 0 1px var(--star-outline-color), 1px 0 var(--star-outline-color), 0 -1px var(--star-outline-color);
  font-size: 4rem;
  line-height: 1;
  /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
  font-family: Times;
  letter-spacing: 3px; }
  .ReviewStar::after {
    content: '★';
    font-family: serif; }
  .ReviewStar:checked, .ReviewStar:hover, .ReviewStar[checked],
  .ReviewStar:checked ~ input,
  .ReviewStar:hover ~ input,
  .ReviewStar[checked] ~ input {
    --star-outline-color: var(--primary-base-color);
    color: var(--primary-base-color); }
  .ReviewStar:focus {
    border: 0; }
