:root {
  --product-information-background: var(--secondary-base-color); }

.ProductInformation-Wrapper {
  padding: 0; }
  @media (min-width: 811px) {
    .ProductInformation-Wrapper {
      padding: 3.6rem 1.2rem; } }

@media (min-width: 811px) {
  .ProductInformation-ExpandableContentHeading {
    font-size: 2rem;
    text-transform: uppercase; } }

@media (max-width: 810px) {
  .ProductInformation-ExpandableContentContent_isContentExpanded {
    padding: 0 1.4rem; } }

@media (min-width: 811px) {
  .ProductInformation-ExpandableContentContent {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4.8rem;
    justify-content: center;
    align-items: flex-start; } }

.ProductInformation-Placeholder {
  margin-top: 2.4rem; }
  @media (max-width: 810px) {
    .ProductInformation-Placeholder {
      margin-top: 2.8rem; } }
  .ProductInformation-Placeholder span {
    display: block;
    margin-bottom: 1.2rem; }
    @media (max-width: 810px) {
      .ProductInformation-Placeholder span {
        margin-bottom: 1.4rem; } }

.ProductInformation-Description, .ProductInformation-Attributes {
  width: 100%; }

.ProductInformation-Attributes {
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 810px) {
    .ProductInformation-Attributes {
      margin-top: 2.8rem; } }

.ProductInformation-ValueLabel, .ProductInformation-AttributeLabel {
  flex: 0 0 50%;
  text-overflow: ellipsis;
  overflow: hidden; }

.ProductInformation-ValueLabel {
  padding-left: .6rem;
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .ProductInformation-ValueLabel {
      padding-left: .7rem;
      margin-bottom: 1.4rem; } }

.ProductInformation-AttributeLabel {
  font-weight: bold; }
  .ProductInformation-AttributeLabel::after {
    content: ':'; }

.ProductInformation-Image {
  padding-bottom: 40%;
  mix-blend-mode: multiply;
  margin-right: 12rem;
  min-width: 36rem;
  background: none; }
  @media (max-width: 810px) {
    .ProductInformation-Image {
      margin-right: 14rem;
      min-width: 42rem;
      display: none; } }
