:root {
  --product-review-item-border: var(--secondary-dark-color); }

.ProductReviewItem {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0 0 1.2rem;
  border-bottom: 1px solid var(--product-review-item-border); }
  @media (max-width: 810px) {
    .ProductReviewItem {
      padding: 0 0 1.4rem; } }
  @media (min-width: 811px) {
    .ProductReviewItem {
      padding: 1.8rem 2.4rem;
      border: 1px solid var(--product-review-item-border); } }
  .ProductReviewItem:last-child {
    margin-bottom: 0; }
    @media (max-width: 810px) {
      .ProductReviewItem:last-child {
        padding-bottom: 0;
        border-bottom: 0; } }
  .ProductReviewItem::before {
    content: none; }
  .ProductReviewItem-ReviewTitle {
    width: 100%;
    margin-bottom: 1.2rem; }
    @media (max-width: 810px) {
      .ProductReviewItem-ReviewTitle {
        margin-bottom: 1.4rem; } }
  .ProductReviewItem-RatingSummary {
    line-height: 1; }
    @media (min-width: 811px) {
      .ProductReviewItem-RatingSummary {
        min-width: 180px; } }
    @media (max-width: 810px) {
      .ProductReviewItem-RatingSummary {
        margin-bottom: 1.4rem;
        width: 100%; } }
  .ProductReviewItem-RatingSummaryItem {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: .6rem; }
    @media (max-width: 810px) {
      .ProductReviewItem-RatingSummaryItem {
        margin-bottom: .7rem; } }
    .ProductReviewItem-RatingSummaryItem:last-of-type {
      margin-bottom: 0; }
    .ProductReviewItem-RatingSummaryItem .ProductReviewRating {
      margin: 0 0 .24rem 1.2rem; }
      @media (max-width: 810px) {
        .ProductReviewItem-RatingSummaryItem .ProductReviewRating {
          margin: 0 0 .28rem 1.4rem; } }
  .ProductReviewItem-ReviewContent {
    flex: 1 0 0;
    margin-left: 4.8rem; }
    @media (max-width: 810px) {
      .ProductReviewItem-ReviewContent {
        flex: 0 0 auto;
        margin-left: 0;
        width: 100%; } }
    .ProductReviewItem-ReviewContent .TextPlaceholder {
      display: block; }
  .ProductReviewItem-ReviewAuthor {
    margin-top: .6rem;
    font-size: .96rem; }
    @media (max-width: 810px) {
      .ProductReviewItem-ReviewAuthor {
        margin-top: .7rem;
        font-size: 1.12rem; } }
  .ProductReviewItem-ReviewDetails {
    margin-bottom: 0; }
    @media (max-width: 810px) {
      .ProductReviewItem-ReviewDetails {
        margin-bottom: 1.4rem; } }
    .ProductReviewItem-ReviewDetails .TextPlaceholder {
      margin-top: .6rem;
      line-height: 1.1; }
      @media (max-width: 810px) {
        .ProductReviewItem-ReviewDetails .TextPlaceholder {
          margin-top: .7rem; } }
      .ProductReviewItem-ReviewDetails .TextPlaceholder::after {
        display: block; }
