.ProductTab-Item {
  margin: 0;
  padding: 0; }
  .ProductTab-Item:last-child {
    margin: 0;
    padding: 0; }
  .ProductTab-Item::before {
    content: ''; }
  .ProductTab-Item_isActive {
    background-color: var(--secondary-base-color); }

.ProductTab-Button {
  margin: 0;
  padding: 0;
  padding: 1.2rem 2rem;
  font-weight: bold; }
