.ProductBundleItem-Wrapper {
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .ProductBundleItem-Wrapper {
      margin-bottom: 1.4rem; } }

.ProductBundleItem-Title {
  font-size: 1.4rem;
  font-weight: bold; }

.ProductBundleItem-Heading {
  font-weight: normal; }
  .ProductBundleItem-HeadingBold {
    font-weight: bold; }

.ProductBundleItem-DropdownWrapper {
  display: flex; }
  .ProductBundleItem-DropdownWrapper .Field_type_number {
    margin-left: 1.44rem; }
    @media (max-width: 810px) {
      .ProductBundleItem-DropdownWrapper .Field_type_number {
        margin-left: 1.68rem; } }

.ProductBundleItem-Select {
  flex-grow: 1; }
